import React from 'react';
import { Row, Col } from 'antd';
import { createMainCard, createSideCard } from './common';
import { infos, apis, sites, resources, tools } from '../data/developer';

const DeveloperPage = () => (
  <Row gutter={24}>
    <Col xl={15} lg={24} md={24} sm={24} xs={24}>
      {createMainCard(' & ', infos)}
      {createMainCard('API & ', apis)}
      {createMainCard('', sites)}
      {createMainCard('', resources)}
    </Col>

    <Col xl={9} lg={24} md={24} sm={24} xs={24}>     
      {createSideCard('', tools)}
    </Col>
  </Row>
)

export default DeveloperPage;