import React from 'react';
import { Row, Col } from 'antd';
import { createSideCard, createMainCard } from '../common';
import { 
  navigations, indexs, infos, evaluations, 
  exchanges, tools, dapps, knowledge, browsers, wallets
} from '../../data/dashboard/home';

const HomePage = () => (
  <Row gutter={24}>
    <Col xl={15} lg={24} md={24} sm={24} xs={24}>
      {createMainCard('', navigations)}
      {createMainCard('', indexs)}
      {createMainCard(' & ', infos)}
      {createMainCard('', evaluations)}
      {
        createMainCard('', exchanges, 
          <span>
            <a href="https://www.feixiaohao.com/exchange/notice" target="_blank" rel="noopener noreferrer">&nbsp;|&nbsp;</a>
            <a href="https://block.cc/notice" target="_blank" rel="noopener noreferrer"></a>
          </span>)
      }
      {createMainCard('', wallets)}
    </Col>

    <Col xl={9} lg={24} md={24} sm={24} xs={24}>
      {createSideCard('', knowledge)}
      {createSideCard('', tools)}
      {createSideCard('BTC | ETH | EOS', browsers)}
      {createSideCard('DAPP', dapps)}
    </Col>
  </Row>
)

export default HomePage;